.landingPageContainer{
  width: 100vw;
  height: 100vh;
  background: url("/public/background.png");
  background-repeat: no-repeat ;
  background-size:cover;
  color: rgb(221, 240, 255);
}
.landingPageContainer nav{
  padding-inline: 1.2rem;
  padding-block:1.6rem ;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.landingPageContainer nav h2{
  font-size: 1.8rem;
  font-weight: 500;
  
}

.landingPageContainer .navlist{
  display: flex;
  gap:1.6rem;
  cursor: pointer;
}

.landingMainContainer{
  
  display: flex;
  justify-content: space-between;
  padding: inline 3rem;
  height: 80vh;
  align-items: center;
}

.landingMainContainer>div:nth-child(1){
  font-size: 1.6rem;
}

.landingMainContainer>div:nth-child(1) div[role='button']{
  background-color: rgb(236, 132, 40);
  width: fit-content;
  height: auto;
  margin-top: 1.5rem;
  padding:0.5rem;
  border-radius: 20px;
  

}
.landingMainContainer>div:nth-child(1) div[role='button'] a {
  text-decoration: none;
  color: rgb(78, 3, 3);
  font-size: 1.3rem;
}

.landingMainContainer>div:nth-child(2) img{
  height :70vh;
  width: auto;
}